/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { colors, breakpoints } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';

const BurgerButton = ({ open, toggle }) => {
  const { theme } = useDarkMode();

  return (
    <button onClick={toggle} css={buttonStyles}>
      <motion.div
        css={line(theme)}
        animate={open ? { rotate: 45, y: 8 } : { rotate: 0, y: 0 }}
        transition={{ duration: 0.3 }}
      />
      <motion.div
        css={line(theme)}
        animate={open ? { opacity: 0 } : { opacity: 1 }}
        transition={{ duration: 0.3 }}
      />
      <motion.div
        css={line(theme)}
        animate={open ? { rotate: -45, y: -8 } : { rotate: 0, y: 0 }}
        transition={{ duration: 0.3 }}
      />
    </button>
  );
};

const buttonStyles = theme => css`
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
  width: 24px;

  @media (min-width: ${breakpoints.md}) {
    display: none;
  }
`;

const line = (theme, open, index) => css`
  height: 3px;
  width: 100%;
  background-color: ${theme === 'light' ? colors.light.text : colors.dark.text};
  transition: all 0.3s ease;

  ${open && index === 1 && 'transform: rotate(45deg) translateY(8px);'}
  ${open && index === 2 && 'opacity: 0;'}
  ${open && index === 3 && 'transform: rotate(-45deg) translateY(-8px);'}
`;

export default BurgerButton;
