/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { colors, spacing } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';

const footerStyles = theme => css`
  padding: ${spacing.lg};
  background-color: #000;
  color: #fff;
  text-align: center;
  font-size: 0.875rem;

  .social {
    margin-bottom: ${spacing.sm};
    display: flex;
    justify-content: center;
    gap: ${spacing.md};

    a {
      color: #fff;
      transition: color 0.3s ease;

      &:hover {
        color: ${colors[theme].accent};
      }
    }
  }
`;

const Footer = () => {
  const { theme } = useDarkMode();
  const year = new Date().getFullYear();

  return (
    <footer css={footerStyles(theme)}>
      <div className="social">
        <a 
          href="https://facebook.com/royaltrustconstructionservices" 
          target="_blank" 
          rel="noopener noreferrer">
            Facebook
        </a>

        <a 
          href="https://linkedin.com/company/royaltrustconstructionservices" 
          target="_blank" 
          rel="noopener noreferrer">
            Linkedin
        </a>

        <a 
          href="https://twitter.com/royaltrustconstructionservices" 
          target="_blank" 
          rel="noopener noreferrer">
            Twitter
        </a>

        <a 
          href="https://wa.me/260766500215" 
          target="_blank" 
          rel="noopener noreferrer">
            WhatsApp
        </a>
      </div>
      
      <div>© {year} SwiftShop. All rights reserved.</div>
    </footer>
  );
};

export default Footer;
