/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { colors, spacing,breakpoints } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';

const projects = [
  { title: 'Bathroom Fixtures', image: '/assets/bathroom.jpg' },
  { title: 'Plumbing Fittings', image: '/assets/plumbing.jpg' },
  { title: 'Building Project', image: '/assets/project.jpg' },
  { title: 'Modern House Design', image: '/assets/villa.jpg' },
  { title: 'Foundation Setup', image: '/assets/foundation.jpg' },
  { title: 'Project on Slab Level', image: '/assets/slab.jpg' },
];

const Projects = () => {
  const { theme } = useDarkMode();

  return (
    <section id="projects" css={sectionStyles(theme)}>
      <h2>Featured Projects</h2>
      <div css={grid}>
        {projects.map((project, i) => (
          <motion.div
            key={i}
            css={card(theme)}
            whileHover={{ y: -10 }}
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
          >
            <motion.img src={project.image} alt={project.title} css={image} />
            <h3>{project.title}</h3>
          </motion.div>
        ))}
      </div>
    </section>
  );
};

const sectionStyles = theme => css`
  padding: ${spacing.sm};
  background: ${colors[theme].background};
  color: ${colors[theme].text};
  text-align: center;
  

  @media (min-width: ${breakpoints.md}) {
    padding: ${spacing.md};
  }
`;

const grid = css`
  display: flex;
  gap: ${spacing.lg};
  justify-content: center;
  flex-wrap: wrap;
`;

const card = theme => css`
  background: ${colors[theme].card};
  padding: ${spacing.md};
  border-radius: 8px;
  width: 100%;
  height: 260px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  transition: transform 0.3s ease;

  @media (min-width: ${breakpoints.md}) {
    width: 330px;
  }
   
  @media (min-width: ${breakpoints.lg}) {
    width: 360px;
    height: 330px;
  }
`;

const image = css`
  width: 100%;
  height: 85%;
  border-radius: 4px;
  }
`;

export default Projects;
