/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { useState } from 'react';
import { colors, spacing, breakpoints } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';
import Toast from './Toast';

const contactStyles = theme => css`
  padding: ${spacing.xl} ${spacing.md};
  background-color: ${colors[theme].background};
  color: ${colors[theme].text};
  text-align: center;

  h2 {
    font-size: 2rem;
    margin-bottom: ${spacing.md};
    color: ${colors[theme].accent};
  }

  p {
    margin-bottom: ${spacing.lg};
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  form {
    display: flex;
    flex-direction: column;
    gap: ${spacing.md};
    max-width: 500px;
    margin: 0 auto;

    @media (min-width: ${breakpoints.md}) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;

      input, textarea {
        flex: 1 1 48%;
      }

      textarea {
        flex-basis: 100%;
      }
    }
  }

  input, textarea {
    padding: ${spacing.sm};
    border: 1px solid ${colors[theme].accent};
    border-radius: 4px;
    background: ${colors[theme].inputBackground};
    color: #000;
    font-size: 1rem;
    box-shadow: inset 0 0 5px 0 rgba(0,0,0,0.7);
  }

  button {
    padding: ${spacing.sm} ${spacing.md};
    background: ${colors[theme].primary};
    color: ${colors[theme].text};
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    transition: background 0.3s ease;

    &:hover {
      scale: 1;
      color: ${colors[theme].text};
      box-shadow: 0 0 10px #0077ff;
    }
  }
`;

function Contact() {
  const { theme } = useDarkMode();
  const [toast, setToast] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });

  const handleChange = (e) =>{
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const form = e.target;
    const data = {
      name: form.name.value,
      email: form.email.value,
      message: form.message.value,
    };
    setFormData({
      name: '',
      email: '',
      message: '',
    });
    ;
    try {
      const res = await fetch('https://your-backend-endpoint.com/contact', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });

      if (res.ok) {
        setToast({ type: 'success', message: 'Message sent successfully!' });
        form.reset();
      } else {
        throw new Error('Failed to send');
      }
    } catch (err) {
      setToast({ type: 'error', message: 'Something went wrong. Try again.' });
    }

    setTimeout(() => setToast(null), 4000);
  };

  return (
    <motion.section
      id="contact"
      css={contactStyles(theme)}
      initial={{ opacity: 0, y: 40 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6 }}
    >
      <h2>Contact Us</h2>
      <p>Have a question about our services or want to work with us? Drop us a message below.</p>
      <form
        name="contact"
        method="POST"
        data-netlify="true"
        onSubmit={handleSubmit}
        >
          <input
           id="name"
           name="name" 
           placeholder="Jane Doe" 
           value={formData.name}
           onChange={handleChange}
           required
           />

          <input 
           id="email" 
           name="email" 
           type="email" 
           placeholder="example@email.com" 
           value={formData.email}
           onChange={handleChange}
           required 
           />

          <textarea 
           id="message" 
           name="message" 
           rows={5} 
           placeholder="Write something..."
           value={formData.message}
           onChange={handleChange} 
           required />

          <button type="submit" >Send Message</button>
      </form>

      {toast && <Toast type={toast.type} message={toast.message} />}
    </motion.section>
  );
};

export default Contact;
