/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { colors, spacing } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';

const About = () => {
  const { theme } = useDarkMode();

  return (
    <section id="about" css={sectionStyles(theme)}>
      <motion.h2
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        About Us
      </motion.h2>
      <motion.p
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ delay: 0.3 }}
      >
        Royal Trust is a forward-thinking construction company, focused on building smarter, faster, and more sustainably. We blend engineering precision with digital innovation to deliver exceptional results.
      </motion.p>
    </section>
  );
};

const sectionStyles = theme => css`
  padding: ${spacing.xl};
  background: ${colors[theme].card};
  color: ${colors[theme].text};
  text-align: center;
  p {
    max-width: 600px;
    margin: 0 auto;
    font-size: 1.1rem;
  }
`;

export default About;
