/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { colors, spacing } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';

const testimonials = [
  {
    name: 'Sarah M.',
    quote: 'Royal Trust transformed our project timeline — they’re fast, reliable, and tech-savvy.',
  },
  {
    name: 'James K.',
    quote: 'Their attention to detail and digital integration is unmatched in the industry.',
  },
];

const Testimonials = () => {
  const { theme } = useDarkMode();

  return (
    <section id="testimonials" css={sectionStyles(theme)}>
      <h2>Testimonials</h2>
      <div css={grid}>
        {testimonials.map((t, i) => (
          <motion.div
            key={i}
            css={card(theme)}
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.5, delay: i * 0.2 }}
          >
            <p>"{t.quote}"</p>
            <span>- {t.name}</span>
          </motion.div>
        ))}
      </div>
    </section>
  );
};

const sectionStyles = theme => css`
  padding: ${spacing.lg};
  background: ${colors[theme].background};
  color: ${colors[theme].text};
  text-align: center;
`;

const grid = css`
  display: flex;
  gap: ${spacing.lg};
  justify-content: center;
  flex-wrap: wrap;
`;

const card = theme => css`
  background: ${colors[theme].card};
  padding: ${spacing.md};
  border-radius: 8px;
  max-width: 340px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  p {
    font-style: italic;
  }
  span {
    display: block;
    margin-top: ${spacing.sm};
    font-weight: bold;
  }
`;

export default Testimonials;
