/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { motion } from 'framer-motion';
import { colors, spacing, breakpoints } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';


const services = [
  { title: 'House Floor Planning', image: '/assets/hardhat.png' },
  { title: 'House Construction', image: '/assets/project.png' },
  { title: 'Home Renovation', image: '/assets/helmet.png' },
  { title: 'Roof Maintainence', image: '/assets/renovation.png' },
  { title: 'Window Installation', image: '/assets/window.png' },
];

const Services = () => {
  const { theme } = useDarkMode();

  return (
    <section id="services" css={sectionStyles(theme)}>
      <h2>Our Services</h2>
      <div css={grid}>
        {services.map((services, i) => (
          <motion.div
            key={i}
            css={card(theme)}
            >
            <motion.img
             src={services.image} 
             alt={services.title}
             css={image} 
             initial={{ rotate: 0 }}
             whileInView={{ rotate: 360 }}
             transition={{ duration: 1 }}
            />
            <h3>{services.title}</h3>
          </motion.div>
        ))}
      </div>
    </section>
  );
};


const sectionStyles = theme => css`
  padding: ${spacing.sm};
  background: ${colors[theme].card};
  color: ${colors[theme].text};
  text-align: center;
  padding-bottom: ${spacing.md};


  @media (min-width: ${breakpoints.md}) {
    padding: ${spacing.xl};
  }
`;

const grid = css`
  display: flex;
  justify-content: center;
  gap: ${spacing.lg};
  flex-wrap: wrap;
`;

const card = theme => css`
  background: ${colors[theme].background};
  padding: ${spacing.md};
  border-radius: 8px;
  width: 100%;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  transition: transform 0.3s ease;
  h3 {
    margin-top: ${spacing.sm};
  }
  
  @media (min-width: ${breakpoints.md}) {
    width: 330px;
  }
`;

const image = css`
  width: 30px;
  height: 30px;
  display: inline-block;
`;

export default Services;
