/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { useState, useRef } from 'react';
import { colors, spacing, breakpoints } from '../constants/theme';
import useDarkMode from '../hooks/useDarkMode';
import BurgerButton from './BurgerButton';
import useOnClickOutside from '../hooks/useOnClickOutside';
import { motion, AnimatePresence } from 'framer-motion';


const Header = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const { theme, toggleTheme } = useDarkMode();
  const menuRef = useRef();
  useOnClickOutside(menuRef, () => setMenuOpen(false));

  return (
    <header css={headerStyles(theme)}>
      <div className="logo">
        <div className="logo-image">
          <img src="/assets/royaltrustlogo.jpg" alt="logo" />
        </div>
        <div className="logo-name">Royal Trust <span>Construction</span></div>
      </div>

      <nav css={navDesktopStyles(theme)}>
        <a href="#hero">Home</a>
        <a href="#about">About</a>
        <a href="#services">Services</a>
        <a href="#projects">Projects</a>
        <a href="#testimonials">Testimonials</a>
        <a href="#contact">Contact</a>
      </nav>

      <div className="actions" css={actionsStyles}>
        <button onClick={toggleTheme} css={iconButton}>🌓</button>
        <BurgerButton open={menuOpen} toggle={() => setMenuOpen(!menuOpen)} />

      </div>

      <AnimatePresence>
        {menuOpen && (
          <motion.nav
            ref={menuRef}
            css={navMobileStyles(theme)}
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ duration: 0.3 }}
          >
            <a href="#hero" onClick={() => setMenuOpen(false)}>Home</a>
            <a href="#about" onClick={() => setMenuOpen(false)}>About</a>
            <a href="#services" onClick={() => setMenuOpen(false)}>Services</a>
            <a href="#projects" onClick={() => setMenuOpen(false)}>Projects</a>
            <a href="#testimonials" onClick={() => setMenuOpen(false)}>Testimonials</a>
            <a href="#contact" onClick={() => setMenuOpen(false)}>Contact</a>
          </motion.nav>
        )}
      </AnimatePresence>
    </header>
  );
};


const headerStyles = theme => css`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ${spacing.md};
  background: ${colors[theme].background};
  color: ${colors[theme].text};
  position: sticky;
  top: 0;
  z-index: 1000;

  .logo {
     font-weight: 600;
     color: ${colors[theme].accent};
     font-size: 1.2rem;
     display: flex;
     gap: 0.5rem;
     align-items: center;
     margin-left: 1rem;
  }
  .logo-image img {
    width: 32px;
    height: 32px;
    border-radius: 50%;
  }
  span {
    color: #ffd900ff;
  }
  
`;

const navDesktopStyles = theme => css`
  display: none;

  @media (min-width: ${breakpoints.md}) {
    display: flex;
    gap: ${spacing.lg};
    a {
      color: ${colors[theme].text};
      text-decoration: none;
      font-weight: 500;

      &:hover {
      color: ${colors[theme].accent};
      }
    }
  }
`;

const navMobileStyles = theme => css`
  position: fixed;
  top: 0;
  right: 0;
  width: 50%;
  height: 100vh;
  background: ${colors[theme].background};
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  padding: ${spacing.xl};

  @media (min-width: ${breakpoints.md}) {
    display: none;
  }

  a {
    margin-bottom: ${spacing.lg};
    color: ${colors[theme].text};
    text-decoration: none;
    font-size: 1.2rem;

  &:hover {
    color: ${colors[theme].accent};
  }
}
`;

const actionsStyles = css`
  display: flex;
  align-items: center;
`;

const iconButton = css`
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
`;




export default Header;
